	module.exports = {"data":{"markdownRemark":{"html":"<h2>“<a href=\"/filez/music/alert.mp3\">ALERT” を聞く</a> (1:26 | [filesize]<a href=\"https://danmaq.com/filez/music/alert.mp3%5B/filesize%5D\">https://danmaq.com/filez/music/alert.mp3[/filesize]</a>)</h2>\n<p>BMS としては初オリジナル。私がミニマルテクノに手を出した最初の作品です。タイトルの由来は知人に聞かせて「なんか電車の遮断機の音みたい」って笑われたことから。  当時 MIDI の音を高音質を保ったままWAVファイル化する方法がわからなかったので、ネットに転がってる音素材をかき集めて、それだけで作ってみた。まさに BM98 簡易サンプラー化。と言っても beatmania 自体サンプラーみたいなものですけどね。まさに人間サンプラー。タイミングずれれば POOR ってことでゲームに怒られる。まさに人機逆転。</p>\n<h2>2009/9/7追記</h2>\n<p>BMS版はNORMAL譜面を追加し、従来譜面の難易度設定を7段階から12段階へ引きなおしました。旧難易度は6(ANOTHER:7)です。(従来譜面は殆ど弄っていません)</p>\n<ul>\n<li>Minimal | 1:26 | 141BpM | BMSLEVEL:N6-H7-A11 | BMSNOTES:N540-H588-A923</li>\n<li>“<a href=\"/filez/music/alert.mid\">ALERT” をMIDI(XG)で聞く</a> ([filesize]<a href=\"https://danmaq.com/filez/music/alert.mid%5B/filesize%5D\">https://danmaq.com/filez/music/alert.mid[/filesize]</a>)</li>\n<li><a href=\"/filez/music/alert.zip\">BMS “ALERT” をダウンロード</a> ([filesize]<a href=\"https://danmaq.com/filez/music/alert.zip%5B/filesize%5D\">https://danmaq.com/filez/music/alert.zip[/filesize]</a>)</li>\n</ul>","frontmatter":{"cover":null,"date":"2000-12-01T00:00:00+09:00","strDate":"2000/11/30","title":"[BMS] ALERT"}}},"pathContext":{"path":"/ja/blog/2000/12/01-bms-alert/","langKey":"ja"}}

